import formatNumber from 'helper/formatNumber';
import usePrevious from 'helper/usePrevious';
import PropTypes from 'prop-types';
import React, { useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { FormattedMessage } from 'react-intl';
import { Button } from 'reactstrap';
import {
    Card,
    CardBody,
    CardHeader,
    CardHeaderToolbar
} from '_metronic/_partials/controls';

InfoCard.propTypes = {
    order: PropTypes.object,
    statusRes: PropTypes.object,
    onUpdate: PropTypes.func
};

function InfoCard({ order = {}, onUpdate = null, statusRes, intl }) {
    const [note, setNote] = useState('');
    const { handleSubmit, register } = useForm();
    const notePrev = usePrevious(order?.note || '');

    useEffect(() => {
        setNote(order?.note || '');
        // notePrev.current = order?.note;
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [order?.note]);

    const handleUpdate = ({ note }) => {
        if (onUpdate) onUpdate(note);
    };

    const handleInputChagnge = e => {
        setNote(e.target.value);
    };

    return (
        <Card className="h-100">
            <CardHeader title={intl.formatMessage({ id: 'ORDER.INFO' })}>
                <CardHeaderToolbar>
                    <Button
                        color="primary"
                        form="form-info-update"
                        disabled={note === notePrev}
                    >
                        <FormattedMessage id="GLOBAL.BUTTON.UPDATE" />
                    </Button>
                </CardHeaderToolbar>
            </CardHeader>
            <CardBody>
                <div className="row form-group">
                    <div className="col-md-6 pr-12">
                        <div className="row align-items-center">
                            <div className="col-4">
                                <span className="order-title">
                                    <FormattedMessage id="ORDER.ID" />
                                </span>
                            </div>
                            <div className="col-8">
                                <div className="form-control bg-light">
                                    {order?.id || ''}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6 pl-12">
                        <div>
                            <div className="row align-items-center">
                                <div className="col-4">
                                    <span className="order-title">
                                        <FormattedMessage id="ORDER.TOTAL_MONEY" />
                                    </span>
                                </div>
                                <div className="col-8">
                                    <div className="form-control bg-light">
                                        {formatNumber(order?.balance)}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="row form-group">
                    <div className="col-md-6 pr-12">
                        <div className="row align-items-center">
                            <div className="col-4">
                                <span className="order-title">
                                    <FormattedMessage id="ORDER.CUSTOMER" />
                                </span>
                            </div>
                            <div className="col-8">
                                <div className="form-control bg-light">
                                    {order?.customer_id || ''}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6 pl-12">
                        <div>
                            <div className="row align-items-center">
                                <div className="col-4">
                                    <span className="order-title">
                                        <FormattedMessage id="ORDER.SURCHARGE" />
                                    </span>
                                </div>
                                <div className="col-8">
                                    <div className="form-control bg-light">
                                        {formatNumber(order?.additional_cost)}
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="row form-group">
                    <div className="col-md-6 pr-12">
                        <div className="row align-items-center">
                            <div className="col-4">
                                <span className="order-title">
                                    <FormattedMessage id="ORDER.STATUS" />
                                </span>
                            </div>
                            <div className="col-8">
                                <div className="form-control bg-light">
                                    {statusRes?.name || order?.status?.name}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6 pl-12">
                        <div className="row align-items-center">
                            <div className="col-4">
                                <span className="order-title">
                                    <FormattedMessage id="ORDER.ORDER_DATE" />
                                </span>
                            </div>
                            <div className="col-8">
                                <div className="form-control bg-light">
                                    {order?.create_at || ''}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="row form-group">
                    <div className="col-md-6 pr-12">
                        <div className="row align-items-center">
                            <div className="col-4">
                                <span className="order-title">
                                    <FormattedMessage id="ORDER.TYPE" />
                                </span>
                            </div>
                            <div className="col-8">
                                <div className="form-control bg-light">
                                    {order?.type?.name || ''}
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className="col-md-6 pl-12">
                        <div className="row align-items-center">
                            <div className="col-4">
                                <span className="order-title">
                                    <FormattedMessage id="ORDER.LAST_UPDATE" />
                                </span>
                            </div>
                            <div className="col-8">
                                <div className="form-control bg-light">
                                    {order?.updated_at || ''}
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className="row form-group">
                    <div className="col-md-6 pr-12">
                        <div className="row align-items-center">
                            <div className="col-4 pr-12">
                                <span className="order-title">
                                    <FormattedMessage id="ORDER.NOTE" />
                                </span>
                            </div>
                            <div className="col-8">
                                <form
                                    onSubmit={handleSubmit(handleUpdate)}
                                    id="form-info-update"
                                >
                                    <input
                                        ref={register}
                                        name="note"
                                        className="form-control"
                                        placeholder={intl.formatMessage({
                                            id: 'GLOBAL.PLACEHOLER.INPUT'
                                        })}
                                        value={note || ''}
                                        onChange={handleInputChagnge}
                                    />
                                </form>
                            </div>
                        </div>
                    </div>
                </div>
            </CardBody>
        </Card>
    );
}

export default InfoCard;
